<script>
import router from "@/router";
import store from "@/store";
import * as THREE from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls.js";
import { MTLLoader } from "three/examples/jsm/loaders/MTLLoader.js";
import { OBJLoader } from "three/examples/jsm/loaders/OBJLoader.js";
import { computed } from "vue";
import { watch } from "vue";

export default {
  name: "product-info",
  data() {
    return {
      type: "photo",
      src: null,
      data: {
        id: 1,
        name: "",
        number: "424023",
        avalibilty: true,
        price: 2000,
        price_dis: 1000,
        description: "     ",
        weight: 10,
        height: 180,
        width: 100,
        dimension: 140,
        d3_img: "3D_Models/Chair.obj",
        d3_mtl: "3D_Models/Chair.mtl",
        src_img: require("@/assets/TestImages/Item/Chair1.jpg"),
        colors: {
          0: {
            id: 1,
            name: "",
          },
          1: {
            id: 1,
            name: "",
          },
        },
        materials: {
          0: {
            id: 1,
            name: "",
          },
          1: {
            id: 1,
            name: "",
          },
        },
        discount: {
          id: 1,
          name: " 10%",
          value: 10,
        },
        photos: {
          0: {
            id: 1,
            src: require("@/assets/TestImages/Item/Chair1.jpg"),
          },
          1: {
            id: 2,
            src: require("@/assets/TestImages/Item/Chair2.jpg"),
          },
          2: {
            id: 3,
            src: require("@/assets/TestImages/Item/Chair3.jpg"),
          },
          3: {
            id: 4,
            src: require("@/assets/TestImages/Item/Chair4.jpg"),
          },
          4: {
            id: 5,
            src: require("@/assets/TestImages/Item/Chair5.jpg"),
          },
          5: {
            id: 6,
            src: require("@/assets/TestImages/Item/Chair6.jpg"),
          },
          6: {
            id: 7,
            src: require("@/assets/TestImages/Item/Chair7.jpg"),
          },
          8: {
            id: 9,
            src: require("@/assets/TestImages/Item/Chair8.jpg"),
          },
        },
      },
      quantity: 1,
    };
  },
  mounted() {
    this.src = this.data.photos[0].src;

    // 3D
    if (this.data.d3_img != null) {
      this.init();
    }
  },
  methods: {
    ShowHide() {
      const element = document.getElementById("popup");
      if (element.style.opacity == 0 || element.style.opacity === "") {
        element.style.opacity = 1;
        element.style.display = "block";
      } else {
        element.style.opacity = 0;
        element.style.display = "none";
      }
    },
    FindInBasket() {
      for (var key in store.state.basket) {
        if (store.state.basket[key].id == this.data.id) {
          return false;
        }
      }
      return true;
    },
    AddToBasket() {
      if (this.quantity >= 1) {
        if (this.FindInBasket()) {
          store.state.basket.push({
            id: this.data.id,
            name: this.data.name,
            price: this.data.price,
            price_dis: this.data.price_dis,
            item_total_price:
              this.data.price_dis != null
                ? this.data.price_dis * this.quantity
                : this.data.price * this.quantity,
            src: this.data.src_img,
            quantity: this.quantity,
          });
          store.state.index++;
          store.state.balance +=
            this.data.price_dis != null
              ? this.data.price_dis * this.quantity
              : this.data.price * this.quantity;
          alert("    ");
          window.localStorage.setItem(
            "basket",
            JSON.stringify(store.state.basket)
          );
          window.localStorage.setItem(
            "index",
            JSON.stringify(store.state.index)
          );
          window.localStorage.setItem(
            "balance",
            JSON.stringify(store.state.balance)
          );
          router.push("/basket");
        } else {
          alert("    ");
        }
      } else {
        alert("  ");
      }
    },
    init() {
      const mycanvas = this.$refs.mymodel;
      const scene = new THREE.Scene();
      const width = computed(() => {
        return window.innerWidth / 2;
      });
      const height = computed(() => {
        return window.innerHeight;
      });
      const aspectRatio = computed(() => {
        return width.value / height.value;
      });
      const updateRenderer = () => {
        renderer.setSize(width.value, height.value);
        renderer.setPixelRatio(window.devicePixelRatio);
      };
      const updateCamera = () => {
        camera.aspect = aspectRatio.value;
        camera.updateProjectionMatrix();
      };
      watch(aspectRatio, updateRenderer);
      watch(aspectRatio, updateCamera);
      const camera = new THREE.PerspectiveCamera(
        100,
        aspectRatio.value,
        1,
        100000
      );
      camera.position.set(10, 4, 0);
      scene.add(camera);

      const ambientLight = new THREE.AmbientLight(0xffffff, 0.5);
      scene.add(ambientLight);

      const directionalLight = new THREE.DirectionalLight(0xffffff, 1);
      directionalLight.position.set(5, 10, 5);
      directionalLight.castShadow = true; // Enable shadow casting
      scene.add(directionalLight);

      const hemisphereLight = new THREE.HemisphereLight(
        0xaaaaaa,
        0x000000,
        0.5
      );
      scene.add(hemisphereLight);

      directionalLight.shadow.mapSize.width = 1024;
      directionalLight.shadow.mapSize.height = 1024;

      directionalLight.shadow.bias = -0.001;

      scene.background = new THREE.Color(0xeeeeee); // Set a background color other than white

      // Create a plane geometry for the ground
      const groundGeometry = new THREE.PlaneGeometry(100, 100);
      const groundMaterial = new THREE.ShadowMaterial({ opacity: 0.5 });
      const ground = new THREE.Mesh(groundGeometry, groundMaterial);
      ground.rotation.x = -Math.PI / 2; // Rotate the ground to be horizontal
      scene.add(ground);

      const mtlLoader = new MTLLoader();
      mtlLoader.load(this.data.d3_mtl, (loadedMaterial) => {
        const objLoader = new OBJLoader();

        // Load the OBJ file with the loaded material
        objLoader.setMaterials(loadedMaterial);
        objLoader.load(this.data.d3_img, (loadedObject) => {
          // Position and add the object to the scene
          loadedObject.position.set(0, 1, 0);
          loadedObject.scale.set(1, 1, 1);
          loadedObject.castShadow = true;
          loadedObject.traverse((child) => {
            if (child.isMesh) {
              child.material.side = THREE.DoubleSide; // Ensure both sides of the material are visible
            }
          });
          scene.add(loadedObject);
        });
      });

      const renderer = new THREE.WebGLRenderer({
        canvas: mycanvas,
        antialias: true,
      });
      renderer.setSize(width.value, height.value);
      renderer.shadowMap.enabled = true;
      const controls = new OrbitControls(camera, renderer.domElement);
      controls.enableDamping = true;
      updateRenderer;
      updateCamera;
      const loop = () => {
        renderer.render(scene, camera);
        controls.update();
        requestAnimationFrame(loop);
      };
      loop();
    },
  },
};
</script>

