                width="24"
                height="24"
                viewBox="0 0 24 24"
                class="eye"
                @click="show_hide_password()"
              >
                <path fill="currentColor" :d="path" />
              </svg>
            </div>
            <button type="submit" class="login-btn mb-2"> </button>
          </form>
          <div class="mb-5 mt-3">
