      }
    },
    login() {
      if (this.email == "") {
        alert("   ");
        return;
      } else if (this.email.search(/^[\w-.]+@([\w-]+.)+[\w-]{2,4}$/) < 0) {
        alert("  ");
        return;
      } else if (this.password == "") {
        alert("    ");
        return;
      }
    },
  },
};
