import { createRouter, createWebHistory } from "vue-router";
import HomeView from "@/views/HomeView.vue";
// Products
import ProductsView from "@/views/Products/ProductsView.vue";
import ProductInfo from "@/views/Products/ProductInfo.vue";
// News
import NewsView from "@/views/News/NewsView.vue";
import NewsInfo from "@/views/News/NewsInfo.vue";
// Basket
import BasketView from "@/views/Basket/BasketView.vue";
import CheckoutView from "@/views/Basket/CheckoutView.vue";
// Favourite
import FavouriteView from "@/views/Favourite/FavouriteView.vue";
// Login
import LoginView from "@/views/LoginView.vue";
// Register
import RegisterView from "@/views/RegisterView.vue";

const routes = [
  {
    path: "/",
    name: "home",
    component: HomeView,
  },
  // Products
  {
    path: "/products",
    name: "products",
    component: ProductsView,
  },
  {
    path: "/product-info",
    name: "ProductInfo",
    component: ProductInfo,
  },
  // News
  {
    path: "/projects",
    name: "News",
    component: NewsView,
  },
  {
    path: "/project-info",
    name: "NewsInfo",
    component: NewsInfo,
  },
  // Basket
  {
    path: "/basket",
    name: "BasketView",
    component: BasketView,
  },
  {
    path: "/checkout",
    name: "CheckoutView",
    component: CheckoutView,
  },
  // Favourite
  {
    path: "/favourite",
    name: "FavouriteView",
    component: FavouriteView,
  },
  // login
  {
    path: "/login",
    name: "LoginView",
    component: LoginView,
  },
  // register
  {
    path: "/register",
    name: "RegisterView",
    component: RegisterView,
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
