<template>
  <div class="news-view">
    <div class="container p-2">
      <div class="bg-whitesmoke my-5">
        <div class="page-title pt-5"></div>
        <div class="page-content my-4 pb-5">
          <NewsCard
            class="m-3"
            v-for="(item, index) in data"
            :key="index"
            :img="item.img"
            :id="item.id"
            :title="item.title"
            :text="item.text"
          ></NewsCard>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import NewsCard from "@/components/NewsCard/NewsCards.vue";
export default {
  name: "news-view",
  components: {
    NewsCard,
  },
  data() {
    return {
      data: {
