          title: " 1",
          text: "            .                   .",
        },
        1: {
          id: 2,
          img: require("@/assets/TestImages/AboutUs/img2.jpg"),
          title: " 2",
          text: "      .               .             .",
        },
        2: {
          id: 3,
          img: require("@/assets/TestImages/AboutUs/img3.jpg"),
          title: " 3",
          text: "         .              .               .",
        },
        3: {
          id: 4,
          img: require("@/assets/TestImages/AboutUs/img4.jpg"),
          title: " 4",
          text: "                        .",
        },
        4: {
          id: 5,
          img: require("@/assets/TestImages/AboutUs/img5.jpg"),
          title: " 5",
          text: "          .                  .",
        },
      },
    };
  },
  methods: {
    readmore() {
      for (var key in this.data) {
        this.data[key].text = this.data[key].text.slice(0, 50);
        this.data[key].text += "...";
      }
    },
  },
  mounted() {
    this.readmore();
  },
};
</script>

