<script>
export default {
  name: "news-info",
  data() {
    return {
      type: "photo",
      src: null,
      videoSrc: null,
      data: {
        id: 1,
        title: " 1",
        text: "            .                   .          .              .               .         .              .               .         .              .               .",
        photos: {
          0: {
            id: 1,
            src: require("@/assets/TestImages/AboutUs/img1.jpg"),
          },
          1: {
            id: 2,
            src: require("@/assets/TestImages/AboutUs/img2.jpg"),
          },
          2: {
            id: 3,
            src: require("@/assets/TestImages/AboutUs/img3.jpg"),
          },
          3: {
            id: 4,
            src: require("@/assets/TestImages/AboutUs/img4.jpg"),
          },
          4: {
            id: 5,
            src: require("@/assets/TestImages/AboutUs/img5.jpg"),
          },
        },
        videos: {
          0: {
            id: 1,
            src: require("@/assets/TestVideos/News.mp4"),
          },
          1: {
            id: 2,
            src: require("@/assets/TestVideos/News2.mp4"),
          },
          2: {
            id: 3,
            src: require("@/assets/TestVideos/News3.mp4"),
          },
          3: {
            id: 4,
            src: require("@/assets/TestVideos/News4.mp4"),
          },
        },
      },
    };
  },
  mounted() {
    this.src = this.data.photos[0].src;
    if (window.localStorage.getItem("video") !== null) {
      this.GetVideo();
    } else {
      this.videoSrc = this.data.videos[0].src;
    }
  },
  methods: {
    async ChangeVideo(src) {
      window.localStorage.setItem("video", src);
      location.reload();
    },
    async GetVideo() {
      this.videoSrc = window.localStorage.getItem("video");
      this.type = "video";
      localStorage.removeItem("video");
    },
    ShowHide() {
      const element = document.getElementById("popup");
      if (element.style.opacity == 0 || element.style.opacity === "") {
        element.style.opacity = 1;
        element.style.display = "block";
      } else {
        element.style.opacity = 0;
        element.style.display = "none";
      }
    },
  },
};
</script>

